/* direction hover: https://codepen.io/FWeinb/pen/GrpqB */
.direction-hover {
    position: relative;
    overflow: hidden;
}
.direction-hover__right {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: inherit;
    -webkit-transition: -webkit-transform 300ms ease-out;
    transition: -webkit-transform 300ms ease-out;
    transition: transform 300ms ease-out;
    transition: transform 300ms ease-out, -webkit-transform 300ms ease-out;
    background-color: white;
    opacity: 0.8;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
.direction-hover__right:before {
    position: absolute;
    content: "";
    width: 42.71%;
    height: 70.71%;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.direction-hover__right:after {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    padding-bottom: 4px;
    font-size: 0.875rem;
    color: rgba(0, 0, 0, 0.8);
    font-weight: 600;
    -webkit-transition: -webkit-transform 450ms ease-out;
    transition: -webkit-transform 450ms ease-out;
    transition: transform 450ms ease-out;
    transition: transform 450ms ease-out, -webkit-transform 450ms ease-out;
}
@media (min-width: 1200px) {
.direction-hover__right:after {
        font-size: 1.167vw;
}
}
@media (min-width: 1600px) {
.direction-hover__right:after {
        font-size: 0.95vw;
}
}
.direction-hover__right:hover {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    z-index: 1
}
.direction-hover__right:hover:after {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
.direction-hover__right:hover:before {
    width: 100%;
    height: 100%;
    -webkit-transform: none;
            transform: none;
}
.direction-hover__right:before {
    width: 42.71%;
    height: 70.71%;
}
.direction-hover__right:before {
    right: 100%;
    bottom: 0;
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
}
.direction-hover__right:after {
    content: attr(data-label);
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
.direction-hover__left {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
